






























import {
	mapState
} from 'vuex'
export default {
	data() {
		return {
			bgimgsrc: 'http://hashcredit.io/html/img/loginbg.png',
			userInfo: '',
			mobile: '',
			verifyCode: '',
			encryptedData: '',
			iv: '',
			code: '',
			losetime: 0,
		};
	},
	computed: {
		...mapState({ //这里的...不是省略号了,是对象扩展符
			$globalData: 'globalData'
		})
	},
	methods: {
		bindLogin() {
			let that = this;
			if (this.mobile.length < 11) {
				uni.showToast({
					icon: 'none',
					title: '手机号输入错误'
				});
				return;
			}
			if (this.verifyCode.length < 4) {
				uni.showToast({
					icon: 'none',
					title: '验证码错误'
				});
				return;
			}
			let data = {
				userCode: this.$store.state.userCode,
				token: this.$store.state.token,
				mobile: this.mobile,
				verificationCode: this.verifyCode,
			}
			console.log(data);
			this.$api.updateMobile(data, function(res) {
				console.log(res);
				if (res.data.status == 1) {
					uni.showToast({
						title: '更新成功',
						mask: false,
						duration: 1500,
						success: function() {
							uni.switchTab({
								url: '../../tabBar/my/my'
							});
						}
					});
				} else {
					uni.showToast({
						title: res.data.msg,
						mask: false,
						icon: 'none',
						duration: 1500
					});
				}
			}, function(err) {
				console.log(err);
			});
		},
		getVerifyCode() {
			let that = this;
			let time = new Date().getTime();
			if (this.losetime > time) {
				let t = parseInt((this.losetime - time) / 1000);
				uni.showToast({
					icon: 'none',
					title: '请等待' + t + '秒后重发'
				});
				return;
			}
			if (this.mobile.length < 11) {
				uni.showToast({
					icon: 'none',
					title: '手机号输入错误'
				});
				return;
			}
			let data = {
				mobile: this.mobile
			}
			this.$api.sendVerifyCode(data, function(res) {
				console.log(res);
				if (res.data.status == 1) {
					let newtime = new Date().getTime();
					that.losetime = newtime + 30000;
					uni.showToast({
						icon: 'success',
						title: '验证码已发送'
					});
				}
			}, function(err) {
				console.log(err);
			});
		}
	},
}



